.pagination {
  display:flex;
  justify-content: space-around;

  width: 158px;
  margin: 0 auto 20px auto;

  &__num{
    text-align: center;
    line-height: 1.5;
  }

  &__link{
    color: $colorBlack;
    text-decoration: none;
    font-size: 18px;
  }
  &__link.m-active{
    color: $colorPrimary
  }

  &__left{
    display: block;
    text-decoration: none;
  }

  &__right{
    display: block;
    text-decoration: none;
  }

}

