.product-card {
  padding: 25px;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  &__image {
    text-decoration: none;
    display: block;
    margin: 20px 0;
    //background: $colorGray400;
    height: 220px;
    overflow: hidden;
    & > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    text-decoration: none;
    color:inherit;
    display: block;
    font-size: 18px;
    font-weight: bold;
    &:hover{
      color: $colorPrimary;
    }
  }
  &__prop {
    margin-top: 20px;
    font-size: 16px;
  }
  &__bottom {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__icon {
    height: 34px;
    width: 34px;
  }
  &__button {
    width:145px;
    margin: 10px 0;
    padding: 9px 20px 5px 20px;
  }
}